.weather-container {

    background-color: $section-bg;
    position: absolute;

    @media screen and (max-width: $tablet) {
        
        box-shadow: 0px 5px 33px rgba(16, 33, 54, 0.6);   
        width: 280px;
        height: 190px;
        left: 20px;
        top: 166px;
        border-radius: 34px;
    }

    @media screen and (min-width: $tablet) and (max-width: $desktop) {

        box-shadow: 0px 8px 43px rgba(16, 33, 54, 0.6);  
        width: 500px;
        height: 300px;
        left: 134px;
        top: 258px;        
        border-radius: 34px;

    }

    @media screen and (min-width: $desktop) {

        box-shadow: 0px 8px 43px rgba(16, 33, 54, 0.6);  
        width: 450px;
        height: 300px;
        left: 830px;
        top: 259px;
        border-radius: 34px 0px 0px 34px;  
    }
}

.weather_section_city_name {
        position: absolute; 
        width: 101px;
        height: 26px;
        font-family: 'Lato';    
        display: flex;
        align-items: center;
        text-align: center;
        color: #767D85;

    @media screen and (max-width: $tablet) {

        left: 110px;
        top: 236px;
        font-size: 16px;
        line-height: 19px;
        }

    @media screen and (min-width: $tablet) and (max-width: $desktop) {

        left: 334px;
        top: 327px;
        font-size: 18px;
        line-height: 22px;
    }

    @media screen and (min-width: $desktop) {

        left: 984px;
        top: 328px;
        font-size: 18px;
        line-height: 22px;
    }
}

.today_current_temp {

        position: absolute;        
        font-family: 'Lato';
        font-weight: 300;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.04em;

    @media screen and (max-width: $tablet) {

        width: 75px;
        height: 56px;
        left: 75px;
        top: 271px;
        font-size: 45px;
        line-height: 54px;
    }

    @media screen and (min-width: $tablet) and (max-width: $desktop) {

        width: 90px;
        height: 96px;
        left: 339px;
        top: 363px;
        font-size: 90px;
        line-height: 108px;
    }

    @media screen and (min-width: $desktop) {
        width: 90px;
        height: 96px;
        left: 989px;
        top: 364px;
        font-size: 90px;
        line-height: 108px;
    }
}

.today_temp_txt {

        position: absolute;
        font-family: 'Lato';     
        display: flex;
        align-items: center;
        text-align: center;
        color: #767D85;

    @media screen and (max-width: $tablet) {

        width: 26px;
        height: 20px;
        left: 170px;
        top: 279px;
        font-size: 14px;
        line-height: 17px;
        }

    @media screen and (min-width: $tablet) and (max-width: $desktop) {

        width: 50px;
        height: 20px;
        left: 324px;
        top: 475px;
        font-size: 18px;
        line-height: 22px;
    }

    @media screen and (min-width: $desktop) {

        width: 50px;
        height: 20px;
        left: 974px;
        top: 476px;
        font-size: 18px;
        line-height: 22px;
    }
}

.today_temp_value{
        position: absolute;
        display: flex;
        align-items: center;
        text-align: center;        
        font-family: 'Lato';
        color: $txt-color;

    @media screen and (max-width: $tablet) {
         
        width: 26px;
        height: 20px;
        left: 170px;
        top: 299px;
        font-size: 16px;
        line-height: 19px;
        
    }
    @media screen and (min-width: $tablet) and (max-width: $desktop) {

        width: 50px;
        height: 20px;
        left: 394px;
        top: 501px;
        font-size: 18px;
        line-height: 22px;
    }
    @media screen and (min-width: $desktop) {
        width: 50px;
        height: 20px;
        left: 1044px;
        top: 502px;
        font-size: 18px;
        line-height: 22px;
        
    }

}